import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'qualifytype', pure: false})
export class QualifyTypePipe implements PipeTransform {

  constructor() {}

  transform(value: any, query: string): any {
    if (!value) return value;
    if((value.name.toLowerCase()).indexOf(query) != -1){
      return true;
    } else if((value.type.toLowerCase()).indexOf(query) != -1) {
      return true;
    } else {
      return false;
    }
  }
}


