import { ModuleWithProviders } from '@angular/core/src/metadata/ng_module';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { AdminComponent } from './admin/admin.component';
import { ViewSightComponent } from './view/sight/sight.component';
import { ViewBusinessComponent } from './view/business/business.component';
import { ViewOrganizationComponent } from './view/organization/organization.component';
import { ViewPostComponent } from './view/post/post.component';

import { AccessControlGuard } from './shared/guards/accesscontrol.service';


export const ROUTES: Routes = [
    {path: '', redirectTo: 'login', pathMatch: 'full'},
    {path: 'login', component: LoginComponent},
    {path: 'admin', loadChildren: './admin/admin.module#AdminModule', canActivate: [AccessControlGuard]},
    {path: 'view/business', component: ViewBusinessComponent, canActivate: [AccessControlGuard]},
    {path: 'view/post', component: ViewPostComponent, canActivate: [AccessControlGuard]},
    {path: 'view/organization', component: ViewOrganizationComponent, canActivate: [AccessControlGuard]},
    {path: 'view/sight', component: ViewSightComponent, canActivate: [AccessControlGuard]},
    {path: 'new', loadChildren: './new/new.module#NewModule', canActivate: [AccessControlGuard]}
];

export const ROUTING: ModuleWithProviders = RouterModule.forRoot(ROUTES);


