import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { AppActions } from '../../store/actions/appActions';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { AppState, DataState } from '../../store/state';
import { AgmCoreModule, MapsAPILoader } from '@agm/core';
import { AgmMap } from '@agm/core';
import * as firebase from 'firebase';
import { checkURL, extractFireBaseFileName, generateUUID } from '../../shared/functions/functions';
import { ImageResult, ResizeOptions } from 'ng2-imageupload';

@Component({
  selector: 'app-viewsight',
  templateUrl: './sight.component.html',
  styleUrls: ['./sight.component.scss']
})
export class ViewSightComponent implements OnInit {

  sight: any;
  latitude: number = 39.8282;
  longitude: number = -98.5795;
  zoom: number = 12;
  logo: string = null;
  img1: string = null;
  img2: string = null;
  img3: string = null;

  @ViewChild('agmMap') agmMap : AgmMap;

  constructor(public db: AngularFireDatabase, private store: Store<AppState>, private router: Router, private ngZone: NgZone) { }

  model: FormGroup = new FormGroup({
    name: new FormControl('', Validators.required),
    type: new FormControl('', Validators.required),
    ranking: new FormControl(1, Validators.required),
    description: new FormControl(''),
  });

  ngOnInit():void {
    this.store.select((state: AppState) => {
        return state.data;
    }).subscribe((data: DataState) => {
        this.sight = data.viewSight;
        this.model.patchValue(data.viewSight);
        this.ngZone.run(() => {
          this.longitude = data.viewSight.longitude;
          this.latitude = data.viewSight.latitude;
        });
        this.logo = this.sight.logo_url;
        this.img1 = this.sight.image1_url;
        this.img2 = this.sight.image2_url;
        this.img3 = this.sight.image3_url;
    });
  }

  selected(imageResult: ImageResult, dest:string): void {
    if (imageResult.error) alert(imageResult.error);
    this[dest] = imageResult.resized
      && imageResult.resized.dataURL
      || imageResult.dataURL;
    this.pushFileToStorage(imageResult, dest);
  }

  pushFileToStorage(imageResult: ImageResult, dest:string): void {
    const storageRef = firebase.storage().ref();
    const uuid = generateUUID();
    const uploadTask = storageRef.child(uuid + "_"  + imageResult.file.name).put(imageResult.file);
 
    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) => {
        const snap = snapshot as firebase.storage.UploadTaskSnapshot
      },
      (error) => {
        console.log(error)
      },
      () => {
        this[dest]=uploadTask.snapshot.downloadURL
      }
    );
  }

  setAddress(place: any): void {
    this.ngZone.run(() => {
      this.latitude = place.geometry.location.lat();
      this.longitude = place.geometry.location.lng();
      this.zoom = 12;
    });
  }

  markerDragEnd(m: any, $event: any): void {
    this.latitude = $event.coords.lat;
    this.longitude = $event.coords.lng;
  }

  submit(): void{
    if(this.model.valid){
      let submissionModel = {
        logo_url: this.logo  || null,
        longitude: this.longitude,
        latitude: this.latitude,
        image1_url: this.img1 || null,
        image2_url: this.img2 || null,
        image3_url: this.img3 || null,
        name: this.model.value.name,
        type: this.model.value.type,
        ranking: this.model.value.ranking,
        zipcode: this.sight.zipcode || null,
        description: this.model.value.description
      };
      let info = this.db.list('/sights/' + this.sight.city + '/');
      info.update(this.sight.$key, submissionModel);
      this.router.navigate(['/admin/sights']);
    }
  }

  delete(): void {
    const storageRef = firebase.storage().ref();
    [this.sight.logo_url, this.sight.image1_url, this.sight.image2_url, this.sight.image3_url].forEach(img => {
      if(checkURL(img)){ storageRef.child(extractFireBaseFileName(img)).delete(); }
    });
    let info = this.db.list('/sights/' + this.sight.city + '/');
    info.remove(this.sight.$key);
    this.router.navigate(['/admin/sights']);
  }

  cancel(): void{
    this.router.navigate(['/admin/sights']);
  }
}


