import * as firebase from 'firebase';
import { ImageResult } from 'ng2-imageupload';

export function getCity(school: string): string {
	if(school == 'UC San Diego') {
	  return 'San Diego';
	} else if(school == 'UC Santa Barbara'){
	  return 'Santa Barbara';
	} else if(school == 'UC Davis'){
	  return 'Davis';
	}
}

export function getSchool(city: string): string {
	if(city == 'San Diego') {
	  return 'UC San Diego';
	} else if(city == 'Santa Barbara'){
	  return 'UC Santa Barbara';
	} else if(city == 'Davis'){
	  return 'UC Davis';
	}
}

export function generateUUID(): string {
	var d = new Date().getTime();
	var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
	    var r = (d + Math.random()*16)%16 | 0;
	    d = Math.floor(d/16);
	    return (c=='x' ? r : (r&0x3|0x8)).toString(16);
	});
	return uuid;
}

export function tConvert (time):string {
	time = time.toString ().match (/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];

	if (time.length > 1) { 
	  time = time.slice (1);
	  time[5] = +time[0] < 12 ? 'AM' : 'PM';
	  time[0] = +time[0] % 12 || 12;
	}
	return time.join ('');
}

export function extractFireBaseFileName(url: string): string {
	return unescape(url.substring(url.lastIndexOf('/')+1, url.lastIndexOf('?')));
}

export function checkURL(url:string): boolean {
	var regexp = /(ftp|http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/
   	return regexp.test(url);
}


