import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'qualifycategory', pure: false})
export class QualifyCategoryPipe implements PipeTransform {

  constructor() {}

  transform(value: any, category: string, query: string): any {
    if (!value) return value;
    if(value.category == category){
      if((value.name.toLowerCase()).indexOf(query) != -1){
        return true;
      } else if((value.type.toLowerCase()).indexOf(query) != -1) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }
}


